@import url("https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600&display=swap");
@keyframes colorChange {
  0% {
    background-image: linear-gradient(to right, #dc2e4e, #dd405d);
  }
  50% {
    background-image: linear-gradient(to right, #4865f4, #5671f5);
  }
  100% {
    background-image: linear-gradient(to right, #ec9e0e, #eca922);
  }
}
@keyframes shadowmove {
  0% {
    -webkit-box-shadow: 0 0 0 76px transparent, 0 0 0 76px rgba(255, 255, 255, 0.1), 0 0 0 45px rgba(255, 255, 255, 0.1), 0 0 0 18px rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 0 76px transparent, 0 0 0 76px rgba(255, 255, 255, 0.1), 0 0 0 45px rgba(255, 255, 255, 0.1), 0 0 0 18px rgba(255, 255, 255, 0.1);
  }
  1% {
    -webkit-box-shadow: 0 0 0 0 transparent, 0 0 0 76px rgba(255, 255, 255, 0.1), 0 0 0 45px rgba(255, 255, 255, 0.1), 0 0 0 18px rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 0 0 transparent, 0 0 0 76px rgba(255, 255, 255, 0.1), 0 0 0 45px rgba(255, 255, 255, 0.1), 0 0 0 18px rgba(255, 255, 255, 0.1);
  }
  24% {
    -webkit-box-shadow: 0 0 0 18px rgba(255, 255, 255, 0.1), 0 0 0 76px transparent, 0 0 0 76px rgba(255, 255, 255, 0.1), 0 0 0 45px rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 0 18px rgba(255, 255, 255, 0.1), 0 0 0 76px transparent, 0 0 0 76px rgba(255, 255, 255, 0.1), 0 0 0 45px rgba(255, 255, 255, 0.1);
  }
  25% {
    -webkit-box-shadow: 0 0 0 18px rgba(255, 255, 255, 0.1), 0 0 0 0 transparent, 0 0 0 76px rgba(255, 255, 255, 0.1), 0 0 0 45px rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 0 18px rgba(255, 255, 255, 0.1), 0 0 0 0 transparent, 0 0 0 76px rgba(255, 255, 255, 0.1), 0 0 0 45px rgba(255, 255, 255, 0.1);
  }
  74% {
    -webkit-box-shadow: 0 0 0 45px rgba(255, 255, 255, 0.1), 0 0 0 18px rgba(255, 255, 255, 0.1), 0 0 0 76px transparent, 0 0 0 76px rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 0 45px rgba(255, 255, 255, 0.1), 0 0 0 18px rgba(255, 255, 255, 0.1), 0 0 0 76px transparent, 0 0 0 76px rgba(255, 255, 255, 0.1);
  }
  75% {
    -webkit-box-shadow: 0 0 0 45px rgba(255, 255, 255, 0.1), 0 0 0 18px rgba(255, 255, 255, 0.1), 0 0 0 0 transparent, 0 0 0 76px rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 0 45px rgba(255, 255, 255, 0.1), 0 0 0 18px rgba(255, 255, 255, 0.1), 0 0 0 0 transparent, 0 0 0 76px rgba(255, 255, 255, 0.1);
  }
  99% {
    -webkit-box-shadow: 0 0 0 76px rgba(255, 255, 255, 0.1), 0 0 0 45px rgba(255, 255, 255, 0.1), 0 0 0 18px rgba(255, 255, 255, 0.1), 0 0 0 76px transparent;
    box-shadow: 0 0 0 76px rgba(255, 255, 255, 0.1), 0 0 0 45px rgba(255, 255, 255, 0.1), 0 0 0 18px rgba(255, 255, 255, 0.1), 0 0 0 76px transparent;
  }
  100% {
    -webkit-box-shadow: 0 0 0 76px rgba(255, 255, 255, 0.1), 0 0 0 45px rgba(255, 255, 255, 0.1), 0 0 0 18px rgba(255, 255, 255, 0.1), 0 0 0 0 transparent;
    box-shadow: 0 0 0 76px rgba(255, 255, 255, 0.1), 0 0 0 45px rgba(255, 255, 255, 0.1), 0 0 0 18px rgba(255, 255, 255, 0.1), 0 0 0 0 transparent;
  }
}
@keyframes opening {
  0% {
    opacity: 0;
    transform: scale(0) rotate(60deg);
  }
  50% {
    transform: scale(0.5) rotate(30deg);
  }
  100% {
    transform: scale(1) rotate(0);
  }
}
@keyframes waving {
  0% {
    opacity: 1;
    transform: rotate(8deg);
  }
  25% {
    transform: rotate(-8deg);
  }
  50% {
    transform: rotate(8deg);
  }
  75% {
    transform: rotate(-8deg);
  }
  100% {
    transform: rotate(0);
  }
}
@keyframes playAgain {
  0% {
    opacity: 0;
    transform: scale(2.2);
  }
  100% {
    opacity: 1;
    transform: scale(0);
  }
}
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
  font-family: "Barlow Semi Condensed", sans-serif;
  background: radial-gradient(#1f3756, #141539);
  width: 100%;
  height: 100%;
  min-height: 100vh;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ol,
ul {
  list-style: none;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

footer {
  padding: 0.625rem 3.125rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
@media (max-width: 39.9375em) {
  footer {
    flex-direction: column-reverse;
    padding-top: 2rem;
  }
}

main {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  height: 33.25rem;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

.heading-result {
  font-size: clamp(1.25rem, 4vw, 1.875rem);
  color: #f1f1f1;
  font-weight: 700;
  text-transform: uppercase;
}

.heading-rules {
  font-size: 1.9375rem;
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 90rem;
  margin: 0 auto;
  width: 100%;
  padding: 0 1.25rem;
}

.sr-only {
  border: 0 !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important;
}

.buttonPick:focus {
  animation: waving 0.4s ease infinite;
}

.btn {
  position: relative;
  display: inline-block;
  border: 0;
  outline: none;
  cursor: pointer;
}
.btn:focus {
  outline: none;
  box-shadow: inset 0 -0.25rem 0 rgba(0, 0, 0, 0.1), 0px 0px 2px 0.15rem #fff, 0 0 1px 0.3rem #2a46c0;
}
.btn:focus-visible {
  box-shadow: inset 0 -0.25rem 0 rgba(0, 0, 0, 0.1), 0 0 1px 0.15rem #fff, 0 0 1px 0.3rem #2a46c0;
}
.btn--modalClose {
  background-color: transparent;
}
.btn--modalClose:hover {
  transform: scale(1.05);
}
.btn--rules {
  border-radius: 0.9375rem;
  border: 0.125rem solid #f1f1f1;
  color: #f1f1f1;
  padding: 0.75rem 2.625rem;
  background-color: transparent;
  font-weight: 700;
  cursor: pointer;
  letter-spacing: 1px;
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  transition-property: color;
  transition-duration: 0.8s;
}
.btn--rules:hover, .btn--rules:focus, .btn--rules:active {
  color: #3b4363;
}
.btn--rules:hover::before, .btn--rules:focus::before, .btn--rules:active::before {
  transform: scaleX(1);
  transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);
}
.btn--rules::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #f1f1f1;
  border-radius: 0.9375rem;
  transform: scaleX(0);
  transform-origin: 0 50%;
  transition-property: transform;
  transition-duration: 0.5s;
  transition-timing-function: ease-out;
}
.btn--reset {
  position: relative;
  color: #3b4363;
  border-radius: 0.625rem;
  padding: 0.875rem 4.375rem;
  font-size: 1.25rem;
}
@media (max-width: 63.9375em) {
  .btn--reset {
    padding: 0.875rem 1.875rem;
  }
}
.btn--reset:active {
  transform: translateX(-0.625rem);
}
.btn--reset:hover {
  transform: scale(0.97);
  transition: all 0.3s ease;
  color: red;
}
.btn--reset:hover::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: #f1f1f1;
  border-radius: 10px;
  animation: playAgain 0.8s ease;
}

.gameboard__game--choices {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 12.5rem;
  width: 12.5rem;
  border-radius: 50%;
  box-shadow: inset 0 -9px 0 0 rgba(0, 0, 0, 0.3), 0 0 0 0 rgba(187, 187, 187, 0.6), 0 0 0 0 rgba(197, 197, 197, 0.6);
  margin: 1.0625rem 2.5rem;
}
@media (max-width: 63.9375em) {
  .gameboard__game--choices {
    height: 8rem;
    width: 8rem;
  }
}
.gameboard__game--choices .choices-icon {
  z-index: 10;
  width: 4.25rem;
}
@media (max-width: 63.9375em) {
  .gameboard__game--choices .choices-icon {
    width: 3.125rem;
  }
}
.gameboard__game--choices::before {
  content: "";
  position: absolute;
  background-color: #f1f1f1;
  border: 1px solid rgba(55, 55, 55, 0.2);
  box-shadow: inset 0 9px 0 0 rgba(90, 90, 90, 0.3);
  border-radius: 100%;
  height: 9rem;
  width: 9rem;
}
@media (max-width: 63.9375em) {
  .gameboard__game--choices::before {
    height: 6rem;
    width: 6rem;
  }
}
.gameboard__game--choices:hover {
  transform: scale(0.98);
  animation: shadowmove 1s infinite, waving 0.4s ease-in-out;
  transition: all 0.3s;
}

.computerChoice,
.playerChoice {
  width: 17rem;
  height: 17rem;
  box-shadow: inset 0 -12px 0 0 rgba(0, 0, 0, 0.3), 0 0 0 0 rgba(187, 187, 187, 0.6), 0 0 0 0 rgba(197, 197, 197, 0.6);
}
.computerChoice .choices-icon,
.playerChoice .choices-icon {
  width: 6rem;
}
@media (max-width: 63.9375em) {
  .computerChoice .choices-icon,
.playerChoice .choices-icon {
    width: 4.0625rem;
  }
}
@media (max-width: 39.9375em) {
  .computerChoice .choices-icon,
.playerChoice .choices-icon {
    width: 2.8125rem;
  }
}
@media (max-width: 63.9375em) {
  .computerChoice,
.playerChoice {
    width: 10rem;
    height: 10rem;
  }
}
@media (max-width: 39.9375em) {
  .computerChoice,
.playerChoice {
    width: 8rem;
    height: 8rem;
    margin: 1.25rem auto 0;
  }
}
.computerChoice::before,
.playerChoice::before {
  content: "";
  position: absolute;
  background-color: #f1f1f1;
  border: 1px solid rgba(55, 55, 55, 0.2);
  box-shadow: inset 0 12px 0 0 rgba(90, 90, 90, 0.3);
  border-radius: 100%;
  height: 13rem;
  width: 13rem;
}
@media (max-width: 63.9375em) {
  .computerChoice::before,
.playerChoice::before {
    width: 8rem;
    height: 8rem;
  }
}
@media (max-width: 39.9375em) {
  .computerChoice::before,
.playerChoice::before {
    width: 6rem;
    height: 6rem;
  }
}
.computerChoice::after,
.playerChoice::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-image: radial-gradient(#1f3756, #141539);
  z-index: -1;
  opacity: 0;
}
.computerChoice.winnerShadow,
.playerChoice.winnerShadow {
  animation: shadowmove 1s infinite !important;
}

.btn--paper {
  background-image: linear-gradient(to right, #4865f4, #5671f5);
}

.btn--rock {
  background-image: linear-gradient(to right, #dc2e4e, #dd405d);
}

.btn--scissor {
  background-image: linear-gradient(to right, #ec9e0e, #eca922);
}

.header {
  position: relative;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.0625rem 1.625rem;
  border: 0.125rem solid #606e85;
  border-radius: 0.625rem;
  margin-top: 3.125rem;
  max-width: 43.75rem;
  width: 100%;
}
@media (max-width: 63.9375em) {
  .header {
    padding: 0.625rem 1.25rem;
    margin-top: 1.5rem;
  }
}
.header__logo > img {
  max-width: 11rem;
}
@media (max-width: 63.9375em) {
  .header__logo > img {
    max-width: 6.25rem;
  }
}
.header__title {
  max-width: 7.5rem;
  color: #3b4363;
}
.header__score {
  display: flex;
  flex-direction: column;
  background-color: #f1f1f1;
  padding: 0.5625rem 1.5625rem;
  border-radius: 0.625rem;
  width: 9.3125rem;
}
@media (max-width: 63.9375em) {
  .header__score {
    width: 5.3125rem;
    padding: 0.3125rem 0.625rem;
  }
}
.header__score--score {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  color: #2a46c0;
}
.header__score .score {
  font-size: clamp(2.5rem, 5vw, 4rem);
  color: #3b4363;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  margin-top: 2.3125rem;
  height: 100%;
  max-width: 43.75rem;
  width: 100%;
}
.container .gameboard {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%;
  height: 100%;
}
.container .gameboard.hide {
  display: none;
}
.container .gameboard::before {
  content: "";
  position: absolute;
  background-image: url("/images/bg-triangle.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  z-index: -1;
  height: 23.75rem;
  width: 50%;
}
.container .gameboard__game {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 44.125rem;
}
@media (max-width: 63.9375em) {
  .container .gameboard__game {
    width: 33.6875rem;
  }
}
@media (max-width: 39.9375em) {
  .container .gameboard__game {
    width: 28.125rem;
  }
}
.container .gameboard__game.hide {
  display: none;
}

.gameboard__result {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: center;
  margin-top: clamp(1.25rem, 7vw, 3.625rem);
  padding: 0 1.25rem;
  max-width: 66.875rem;
  width: 100%;
  visibility: hidden;
  opacity: 0;
  transition: transform 1s ease, opacity 0.2s ease-in;
  z-index: -1;
}
.gameboard__result.hide {
  opacity: 1;
  visibility: visible;
  z-index: 1;
}
.gameboard__result--text {
  position: absolute;
  top: 50%;
  left: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  opacity: 0;
  transform: scale(0);
  visibility: hidden;
}
@media (max-width: 39.9375em) {
  .gameboard__result--text {
    top: 126%;
    left: 50%;
  }
}
.gameboard__result--text.show {
  opacity: 1;
  transform: scale(1) translate(-50%, -50%);
  visibility: visible;
  transition: all 0.8s ease;
  transition-delay: 1s;
}
.gameboard__result .result-text {
  font-size: clamp(1.875rem, 5vw, 3.125rem);
  color: #f1f1f1;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-bottom: 1.25rem;
}

.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 60;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translate(-500%);
  background-color: rgba(0, 0, 0, 0.4);
  opacity: 0;
  transition: transform 0.5s ease-in-out;
  padding: 0 0.375rem;
}
.modal-container.show-modal {
  transform: translate(0);
  opacity: 1;
}

.modal {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border-radius: 0.625rem;
  padding: 3rem 2rem;
}
.modal__top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.875rem;
}